.header{
  height: 80px;
  background:black;
  width: 100%;
}
.header-content{
  width: 40%;
  margin: 0 auto;
}
.header-title{
  float: left;
  width: 100px;
  line-height: 50px;
  color: #DDD;
  font-size: 24px;
  cursor: pointer;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;

}
.content{
width: 600px;
margin: 0 auto;
text-align: left;
}
.list-title{
position: relative;
display: block;
}
.title-numb{
position: absolute;
top: 2px;
right: 5px;
display: inline-block;
font-size: 14px;
}
.list input{
position: absolute;
top: 2px;
left: 10px;
width: 22px;
height: 22px;
cursor: pointer;
}
.list li{
height: 32px;
line-height: 32px;
background: #fff;
position: relative;
margin-bottom: 10px;
text-indent: 0;
padding: 0 45px;
border-left: 5px solid #629A9C;
list-style:none
}
.list1 input{
position: absolute;
top: 2px;
left: 10px;
width: 22px;
height: 22px;
cursor: pointer;
}
.list1 li{
height: 32px;
line-height: 32px;
background: #fff;
position: relative;
margin-bottom: 10px;
text-indent: 0;
padding: 0 45px;
border-left: 5px solid #629A9C;
list-style:none;
border-left-color: #999;

}
.list span{
position: absolute;
top: 2px;
right: 5px;
display: inline-block;
width: 14px;
height: 12px;
border-radius: 14px;
border: 6px double #FFF;
background: #CCC;
line-height: 14px;
text-align: center;
color: #FFF;
font-weight: bold;
font-size: 14px;
cursor: pointer;

}
